import db from '../../db'
import { guid } from '../../libs/utils'

const state = {
	data: [],
	selection: {}
};

db.goods.toArray().then((results) => {
	state.data.push(...results);
	state.data.forEach((row) => {
		state.selection[row.gid] = false
	})
})

export default {
	namespaced: true,
	state,
	mutations: {
		update(state, row) {
			//Object.assign(this.indexes[row.gid], row)
			state.data.some((item) => {
				if(item.gid === row.gid) {
					Object.assign(item, row);
					return true
				}
			})
		},
		add(state, row) {
			state.data.push(row);
			state.selection[row.gid] = false
		}
	},
	actions: {
		put({
			commit,
			getters,
			dispatch
		}, row) {
			if(row.gid) {
				row.modifytime = Date.now();
				return dispatch("update", row)
			} else {
				row.gid = guid();
				row.sort = (getters.sortlist[0] || {
					sort: 1
				}).sort - 1;
				row.createtime = Date.now();
				return dispatch("add", row)
			}
		},
		update({
			commit
		}, row) {
			return db.goods.update(row.gid, row).then(() => {
				commit("update", row)
				//Object.assign(this.indexes[row.gid], row)
			})
		},
		add({
			commit
		}, row) {
			return db.goods.add(row).then(() => {
				commit("add", row)
			})
		},
		sort({
			state,
			dispatch
		}, {
			index,
			toIndex
		}) {
						state.data.splice(toIndex, 0, ...state.data.splice(index, 1));
						state.data.forEach((row, i) => {
							dispatch("update", {
								gid: row.gid,
								sort: i
							})
						})
//			let a, b;
//			state.data.forEach((row, i) => {
//				if(i === index) {
//					a = i
//				} else if(i === toIndex) {
//					b = i
//				} else {
//					row.sort = i
//				}
//			})
//
//			state.data[index].sort = b;
//			state.data[toIndex].sort = a;
//			
//			state.data.forEach(({gid}, sort) => {
//				dispatch("update", {
//					gid,
//					sort
//				})
//			})
		}
	},
	getters: {
		get(state, getters) {
			return(gid) => getters.indexes[gid]
		},
		sortlist(state) {
			return state.data.slice(0).sort((a, b) => {
				return a.sort > b.sort ? 1 : -1
			})
		},
		indexes(state) {
			const indexes = {};
			state.data.forEach(item => {
				indexes[item.gid] = item
			});
			return indexes
		}
	}
}