function digitLength(number) {
	// Get digit length of e
	const eSplit = number.toString().split(/[eE]/),
		len = (eSplit[0].split('.')[1] || '').length - (+(eSplit[1] || 0));
	return len > 0 ? len : 0;
}

function strip(num, precision = 12) {
	return +parseFloat(num.toPrecision(precision))
}

function zoom(number, ratio = digitLength(number)) {
	const numberString = number.toString();
	let result = Number(numberString.replace('.', ''));
	if(ratio && numberString.indexOf(".") < 0) {
		result = number * Math.pow(10, ratio)
	}
	return result
}

/**
 * 遍历参数列表
 * @param {*arguments} args
 * @param {*function} fn
 */
function eachArguments(args, fn) {
	[].forEach.call(args, fn)
}

/**
 * 精确乘法
 */
function times() {
	//	var digitCount = 0,
	//		result = 1;
	//	eachArguments(arguments, function(number, i, array) {
	//		digitCount += digitLength(number);
	//		result *= zoom(number);
	//	})
	//
	//	return result / Math.pow(10, digitCount)
	const number1 = arguments[0],
		number2 = arguments[1],
		pow = digitLength(number1) + digitLength(number2);
	let result = zoom(number1) * zoom(number2);
	if(pow) {
		result /= Math.pow(10, pow); //times(result, Math.pow(10, pow))
	}
	if(arguments.length > 2) {
		let args = [].slice.call(arguments, 2);
		args.unshift(result);
		return times.apply(null, args)
	}
	return result
}

/**
 * 精确加法
 */
function plus() {
	let count = 0;
	const baseNum = Math.pow(10, Math.max.apply(Math, [].map.call(arguments, number => digitLength(number))));

	eachArguments(arguments, number => {
		count += strip(number * baseNum)
	});

	return count / baseNum
}

/**
 * 精确减法
 */
function minus() {
	let count = 0;
	const baseNum = Math.pow(10, Math.max.apply(Math, [].map.call(arguments, number => digitLength(number))));

	eachArguments(arguments, (num, i) => {
		count -= strip((i ? num : -num) * baseNum)
	})

	return count / baseNum
}

/**
 * 精确除法
 */
function divide() {
	const number1 = arguments[0],
		number2 = arguments[1],
		pow = digitLength(number2) - digitLength(number1);
	let result = zoom(number1) / zoom(number2);
	if(pow) {
		result = times(result, Math.pow(10, pow))
	}
	if(arguments.length > 2) {
		let args = [].slice.call(arguments, 2);
		args.unshift(result);
		return divide.apply(null, args)
	}
	return result
}

/**
 * 四舍五入
 */
function round(num, ratio) {
	const base = Math.pow(10, ratio);
	return divide(Math.round(times(num, base)), base);
}

Math.times = times;
Math.divide = divide;
Math.plus = plus;
Math.minus = minus;

export {
	times,
	divide,
	plus,
	minus,
	round
}